import Peaks from "peaks.js";
import { useState, useEffect } from "react";
export function usePeaks({ audioRef, waveformRef, options, }) {
    const [peaks, setPeaks] = useState();
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    useEffect(() => {
        setLoading(true);
        if (!audioRef.current) {
            setError(new Error("no audio ref"));
            setLoading(false);
            return;
        }
        if (!waveformRef.current) {
            setError(new Error("no waveform ref"));
            setLoading(false);
            return;
        }
        if (audioRef.current && waveformRef.current) {
            const peaksOptions = {
                overview: { container: waveformRef.current },
                mediaElement: audioRef.current,
                ...options,
            };
            Peaks.init(peaksOptions, (error, peaks) => {
                if (error)
                    setError(error);
                if (peaks)
                    setPeaks(peaks);
                setLoading(false);
            });
        }
        // Clean up on unmount
        return () => {
            peaks?.destroy();
        };
    }, [audioRef.current, waveformRef.current, options]);
    return { peaks, loading, error };
}
